.descriptionBlock {
  padding: 10px;
  display: flex;
}
.descriptionBlock img {
  height: 200px;
  width: 180px;
  border-radius: 30px;
}
.pageTop {
  padding: 5px;
  border-bottom: 1px solid black;
  width: 500px;
}
.rightBlock {
  margin-left: 30px;
  padding: 5px;
  border-bottom: 1px solid black;
}
.pageBottom {
  padding-top: 20px;
}
.name {
  font-size: 1.2em;
  font-weight: bold;
  margin-bottom: 10px;
}
.contacts {
  padding-left: 20px;
}
.inputFile {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}
.inputFile + label {
  background-color: #edeef0;
  border-radius: 0px 0px 100px 100px ;
  padding: 3px 51px;
  opacity: 0.5
}
.inputFileDiv {
  position: relative;
  top: -25px;
}
.editModeButton {
  position: absolute;
  top: 300px;
  left: 340px;
  text-align: center;
  width: 150px;

}
.editModeButton:hover {
  background-color: #edeef0;
  border-radius: 10px;
}
.editModeButton img {
  height: 15px;
  width: 15px;
}
.formSummaryError {
  border: 1px solid red;
  padding: 5px;
  color: red;
}